// Typography
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.25;
  font-weight: 600;
  color: #000000;
  margin: 0;
}

.h1,
h1 {
  font-size: rem(44);
  line-height: 55px;
  @media(max-width: 767px){
    font-size: rem(30);
    line-height: 1.2;
  }
}

.h2,
h2 {
  font-size: rem(36);
  @media(max-width: 767px){
    font-size: rem(28);
  }
}

.h3,
h3 {
  font-size: rem(28);
  @media(max-width: 767px){
    font-size: rem(20);
  }
}

.h4,
h4 {
  font-size: rem(24);
}

.h5,
h5 {
  font-size: rem(20);
}

.h6,
h6 {
  font-size: rem(16);
}

p{
  font-size: rem(20);
  line-height: 25px;
  @media(max-width: 767px){
    font-size: rem(16);
  }
}

a{
  color: $primaryColor;
  transition: .3s ease-in-out;
}

